import React, {PropTypes} from 'react';
import KlassName from 'teapan/klassName';
import { SKINS, MODES } from '../../constant/props';

class Menu extends React.Component {
	constructor(props) {
		super(props);
	}

	render() {
		const props = this.props;
		const {
			skin,
			mode,
			className,
			children,
			...others
		} = props;

		const klassName = KlassName(
			'menu',
			{
				['menu--' + skin]:skin,
				['menu--' + mode]:mode,
			},
			className
		);

		return (
			<ul
				{...others}
				className={klassName}
			>
				{children}
			</ul>
		);
	}
};

Menu.propTypes = {
	skin: PropTypes.oneOf(SKINS),
	mode: PropTypes.oneOf(MODES),
	className: PropTypes.string,
	children: PropTypes.node,
};

Menu.defaultProps = {
	mode: 'vertical',
};

export default Menu;